.storeLocator {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .title {
    text-align: center;
    margin-bottom: 30px;
    color: #333;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .mapContainer {
    width: 100%;
    height: 600px;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .storeList {
    background-color: #f8f8f8;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .storeList h2 {
    margin-top: 0;
    margin-bottom: 20px;
    color: #333;
  }
  
  .storeList ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .storeList li {
    cursor: pointer;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    transition: background-color 0.3s ease;
    flex: 1 1 calc(33.333% - 20px);
    min-width: 200px;
  }
  
  .storeList li:hover {
    background-color: #e9e9e9;
  }
  
  .storeList h3 {
    margin: 0 0 5px 0;
    color: #444;
  }
  
  .storeList p {
    margin: 0;
    color: #666;
  }
  
  @media (max-width: 768px) {
    .storeList li {
      flex: 1 1 100%;
    }
  }