.searchResults {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .searchResults h1 {
    font-size: 1.5rem;
    margin-bottom: 20px;
    color: #333;
  }
  
  .productsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px 0;
  }
  
  @media (max-width: 768px) {
    .searchResults {
      padding: 10px;
    }
    
    .productsGrid {
      grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
      gap: 10px;
    }
  } 