.forgotPasswordPage {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f5f5f5;
    font-family: 'Playfair Display', serif;
  }
  
  .forgotPasswordContainer {
    width: 90%;
    max-width: 500px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    padding: 40px;
  }
  
  .formHeader {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .forgotPasswordTitle {
    font-size: 2rem;
    color: #333;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  
  .forgotPasswordSubtitle {
    font-size: 1rem;
    color: #666;
    text-align: center;
    margin-bottom: 30px;
  }
  
  .inputGroup {
    position: relative;
    margin-bottom: 20px;
  }
  
  .input {
    width: 100%;
    padding: 12px 40px;
    border: none;
    border-bottom: 2px solid #ddd;
    font-size: 1rem;
    transition: all 0.3s ease;
    background-color: transparent;
  }
  
  .input:focus {
    outline: none;
    border-color: #4f46e5;
  }
  
  .inputIcon {
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    color: #666;
  }
  
  .successMessage {
    background-color: #dcfce7;
    color: #166534;
    padding: 12px;
    border-radius: 8px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .errorMessage {
    background-color: #fee2e2;
    color: #dc2626;
    padding: 12px;
    border-radius: 8px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .submitButton {
    width: 100%;
    padding: 12px;
    background-color: #4f46e5;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    transition: background-color 0.3s ease;
    margin-top: 20px;
  }
  
  .submitButton:hover {
    background-color: #4338ca;
  }
  
  .backToLogin {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    margin-top: 20px;
    color: #4f46e5;
    text-decoration: none;
    font-size: 0.9rem;
    transition: color 0.3s ease;
  }
  
  .backToLogin:hover {
    color: #4338ca;
  }